@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
html {
  box-sizing: border-box;
}

*::after::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-height: 100vh;
  background-color: rgb(238, 238, 238);
  font-family: 'Nunito', sans-serif;
}

header {
  width: 100%;
  height: 200px;
  max-height: 200px;
  background-color: rgb(239, 14, 61);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

header img {
  object-fit: cover;
  width: 400px;
  height: auto;
  background-color: rgb(239, 14, 61);
}

.black-line {
  height: 20px;
  background-color: rgb(56, 56, 56);
}

.container {
  width: clamp(320px, 75%, 900px);
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 15px;
  gap: 15px;
}

.card {
  border: 1px solid black;
  border-radius: 25px;
  margin: auto;
  padding: 18px;
  padding-bottom: 0;
  margin-top: 20px;
  text-align: center;
  box-shadow: 5px 5px 5px rgb(0, 0, 0, 0.1), -5px -5px 5px rgb(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  font-size: 1.4rem;
  width: 120px;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.card img {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}

.score {
  width: clamp(320px, 75%, 900px);
  display: flex;
  justify-content: space-around;
  margin: auto;
  flex-wrap: wrap;
  margin-top: 15px;
}

.score-tag {
  border-radius: 25px;
  padding: 10px;
  font-size: 1.2rem;
  width: 200px;
  text-align: center;
  box-shadow: 1px 4px 1px rgb(0, 0, 0, 0.1);
  margin-bottom: 15px;
}

.best-score {
  background-color: rgb(239, 14, 61, 0.5);
}

.current-score {
  background-color: rgb(181, 255, 181);
}

.overlay {
  height: 150%;
  width: 100%;
  position: absolute;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.1);
}

.message-card {
  background-color: rgb(181, 255, 181);
  padding: 95px;
  font-size: 2rem;
  border-radius: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
}

.message-card p {
  margin-top: 0;
}

.message-card button {
  border: 1px transparent;
  padding: 15px;
  font-size: 1.2rem;
  border-radius: 25px;
  cursor: pointer;
  background-color: rgb(239, 14, 61, 1);
  color: white;
}

.message-card button:hover {
  background-color: rgb(201, 14, 54);
}

.lose {
  background-color: rgb(239, 14, 61, 1);
}

.lose button {
  background-color: rgb(17, 190, 17);
}

.lose button:hover {
  background-color: rgb(181, 255, 181);
}

@media screen and (min-width: 768px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
  .card {
    width: 130px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    grid-template-columns: repeat(4, 1fr);
  }
  .card {
    width: 140px;
  }
}

